import { produce } from "immer";
import { FormEvent, useState } from "react";
import { ILoginState, loginProperty } from "../types/LoginTypes";
import Login from "../views/Login";

export default function LoginContainer(): JSX.Element {
  const [state, setState] = useState<ILoginState>({
    email: "",
    password: "",
  });

  /**
   *     .
   * @param target  ,  
   * @param value   
   */
  // N.B.:   setState   .
  function onUpdate(target: loginProperty, value: string) {
    setState(
      produce(state, (draft) => {
        draft[target] = value;
      })
    );
  }

  /**
   *         ,  
   *    ,   .
   * @param e FormEvents  
   */
  function onSubmit(e: FormEvent) {
    //     state  
    //         (   ):
    const data = new FormData(e.target as HTMLFormElement);
    console.log(JSON.stringify(Object.fromEntries(data.entries())));

    e.preventDefault();

    setState(
      produce(state, (draft) => {
        draft.email = "";
        draft.password = "";
      })
    );
  }

  return (
    <Login
      onInput={onUpdate}
      onSubmit={onSubmit}
      renderContent={true}
      {...state}
    />
  );
}
