import "./App.css";
import { Provider } from "react-redux";
import { store } from "./stores/redux/store";

interface IProps {
  children?: JSX.Element;
}

/**
 * ,   redux   .
 * @param props   
 * @returns react 
 */
function AppRedux(props: IProps) {
  return (
    <>
      <Provider store={store}>{props.children}</Provider>
    </>
  );
}

export default AppRedux;
