import { LoginStoreProvider, loginStore } from "./stores/mobx/loginModel";

interface IProps {
  children?: JSX.Element;
}

/**
 * ,      Mobx-state-tree.
 * @param props   
 * @returns react 
 */
function AppMst(props: IProps) {
  return (
    <>
      <LoginStoreProvider value={loginStore}>
        {props.children}
      </LoginStoreProvider>
    </>
  );
}

export default AppMst;
